import React from 'react';
import { Col } from 'react-bootstrap';
import ParticipantInput from '../components/ParticipantInput';  // Adjust the import path if necessary

function DefineGroup({ participants, setParticipants, onSubmit }) {
  return (
    <Col md={12}>
      <ParticipantInput 
        participants={participants} 
        setParticipants={setParticipants} 
        onSubmit={onSubmit} 
      />
    </Col>
  );
}

export default DefineGroup;