import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Routes, Route, useNavigate } from 'react-router-dom';
import { Container } from 'react-bootstrap';
import { useTranslation } from 'react-i18next';
import './i18n';  // Import i18n configuration
import Header from './components/Header';
import DefineGroup from './routes/defineGroup.js';
import Expenses from './routes/expenses.js';

function App() {
  useEffect(() => {
    // Initialize Kakao SDK
    if (window.Kakao && !window.Kakao.isInitialized()) {
      window.Kakao.init('877b5258147310dfd661a9ebea2e615f'); // Your Kakao JavaScript Key
    }
  }, []);

  const { i18n } = useTranslation();
  const [expenses, setExpenses] = useState([]);
  const [participants, setParticipants] = useState([]);
  const navigate = useNavigate(); // Use the useNavigate hook for navigation

  const changeLanguage = (lng) => {
    i18n.changeLanguage(lng);
  };

  const handleParticipantsSubmit = () => {
    if (participants.length > 0) {
      // Navigate to the expense entry page
      navigate('/expenses');
    } else {
      alert('Please add at least one participant');
    }
  };

  return (
    <div>
      <Header changeLanguage={changeLanguage} language={i18n.language} />
      <Container>
        <Routes>
          <Route
            path="/"
            element={<DefineGroup participants={participants} setParticipants={setParticipants} onSubmit={handleParticipantsSubmit} />}
          />
          <Route
            path="/expenses"
            element={<Expenses participants={participants} setParticipants={setParticipants} expenses={expenses} setExpenses={setExpenses} />}
          />
          <Route
            path="/:groupId"
            element={<Expenses participants={participants} setParticipants={setParticipants} expenses={expenses} setExpenses={setExpenses} />}
          />
        </Routes>
      </Container>
    </div>
  );
}

export default App;
