import React, { useState } from 'react';
import { Modal, Button, Form, ListGroup } from 'react-bootstrap';
import { useTranslation } from 'react-i18next';

function ManageParticipantsModal({ show, handleClose, participants, setParticipants, expenses }) {
  const { t } = useTranslation();
  const [newParticipant, setNewParticipant] = useState('');

  const handleAddParticipant = () => {
    if (newParticipant && !participants.includes(newParticipant)) {
      setParticipants([...participants, newParticipant]);
      setNewParticipant('');
    }
  };

  const handleDeleteParticipant = (participant) => {
    const isPayer = expenses.some(expense => expense.paidBy === participant);
    const isBeneficiary = expenses.some(expense => expense.beneficiaries.includes(participant));

    if (!isPayer && !isBeneficiary) {
      setParticipants(participants.filter(p => p !== participant));
    } else {
      alert(t('expenseForm.cannotDeleteParticipant'));
    }
  };

  return (
    <Modal show={show} onHide={handleClose}>
      <Modal.Header closeButton>
        <Modal.Title>{t('manageParticipants.title')}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className='d-flex align-items-center mb-3'>
            <Form.Control
                type="text"
                placeholder={t('expenseForm.addParticipantPlaceholder')}
                value={newParticipant}
                style={{ flex: '1', marginRight: '10px' }}
                onChange={(e) => setNewParticipant(e.target.value)}
            />
            <Button variant="secondary" className='' onClick={handleAddParticipant}>
                {t('expenseForm.addParticipantButton')}
            </Button>
        </div>
          
        <h6>{t('manageParticipants.existingParticipants')}</h6>
        {participants.map((participant, index) => (
          <ListGroup key={index} className="">
            <ListGroup.Item className="d-flex justify-content-between align-items-center">
                {participant}
                <Button
                variant="danger"
                size="sm"
                onClick={() => handleDeleteParticipant(participant)}
                disabled={expenses.some(expense =>
                    expense.paidBy === participant || expense.beneficiaries.includes(participant)
                )}
                >
                    {t('expenseForm.delete')}
                </Button>
            </ListGroup.Item>
            
          </ListGroup>
        ))}
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>
          {t('manageParticipants.close')}
        </Button>
      </Modal.Footer>
    </Modal>
  );
}

export default ManageParticipantsModal;
