import React from 'react';
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { useTranslation } from 'react-i18next';
import { Button } from 'react-bootstrap';

function ShareButton({ groupId, summaryText, saveDocument }) {

    const { t } = useTranslation();

    const shareSummary = async () => {
      try {
        await saveDocument();
        if (window.Kakao) {
          window.Kakao.Link.sendDefault({
            objectType: 'text',
            text: summaryText,
            link: {
              mobileWebUrl: `https://huido93.github.io/expense-settler/${groupId}`,
              webUrl: `https://huido93.github.io/expense-settler/${groupId}`
            }
          });
        }
      } catch (error) {
        console.error('Error saving before sharing:', error);
      }
    };
  
    return (
      <Button variant='warning' className='w-100 mt-2' onClick={shareSummary}>
        {t('shareButton.kakaotalk')}
      </Button>
    );
  }

  export default ShareButton