import React from 'react';
import axios from './../utils/axios.js';
import { useNavigate } from 'react-router-dom';
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Card, Button, Row, Col, Alert } from 'react-bootstrap';
import { useTranslation } from 'react-i18next';
import ExpenseList from './ExpenseList';
import TransactionsList from './TransactionsList';
import ShareButton from './ShareButton';

function calculateSettlement(expenses, participants) {
  const totalExpenses = expenses.reduce((sum, expense) => sum + parseFloat(expense.amount), 0);
  const balances = participants.reduce((acc, participant) => {
    acc[participant] = 0;
    return acc;
  }, {});

  expenses.forEach((expense) => {
    const splitAmount = expense.amount / expense.beneficiaries.length; // Divide amount among beneficiaries

    expense.beneficiaries.forEach(beneficiary => {
      balances[beneficiary] -= splitAmount; // Subtract split amount from each beneficiary
    });

    balances[expense.paidBy] += expense.amount; // Add the full amount to the payer's balance
  });

  const transactions = [];
  const creditors = [];
  const debtors = [];

  for (let participant in balances) {
    if (balances[participant] > 0) {
      creditors.push({ participant, amount: balances[participant] });
    } else if (balances[participant] < 0) {
      debtors.push({ participant, amount: -balances[participant] });
    }
  }

  while (creditors.length > 0 && debtors.length > 0) {
    const creditor = creditors[0];
    const debtor = debtors[0];

    const minAmount = Math.min(creditor.amount, debtor.amount);

    transactions.push({
      from: debtor.participant,
      to: creditor.participant,
      amount: minAmount,  // Keep this as a number for now
    });

    creditor.amount -= minAmount;
    debtor.amount -= minAmount;

    if (creditor.amount === 0) creditors.splice(0, 1);
    if (debtor.amount === 0) debtors.splice(0, 1);
  }

  return { totalExpenses, transactions };
}

function formatCurrency(amount, currency) {
  const options = {
    style: 'currency',
    currency: currency,
    minimumFractionDigits: 0,
    maximumFractionDigits: 2,
  };
  return amount.toLocaleString(undefined, options);  // Amount is expected to be a number
}

function SettlementSummary({ groupId, expenses, setExpenses, expenseCopy, setExpenseCopy, participants, removeExpense, setGroupId }) {
  const { t, i18n } = useTranslation();
  const navigate = useNavigate();


  // Automatically set currency based on the language
  const currency = i18n.language === 'ko' ? 'KRW' : 'USD';

  if (participants.length === 0) return null;

  const { totalExpenses, transactions } = calculateSettlement(expenses, participants);

  // Create the summary text with translations and formatted currency
  const summaryText = `
   ${t('settlementSummary.header')}
   ${t('settlementSummary.totalSpent')}: ${formatCurrency(totalExpenses, currency)}
   ${t('settlementSummary.numParticipants')}: ${participants.length}
   ${t('settlementSummary.detailedExpenses')}:
    ${expenses.map(exp => ` ${exp.paidBy}: ${formatCurrency(exp.amount, currency)} ${t('settlementSummary.for')} ${exp.description}`).join('\n')}
   ${t('settlementSummary.transactions')}:
    ${transactions.map(tx => `${tx.from} ${t('settlementSummary.owes')} ${tx.to} ${formatCurrency(tx.amount, currency)}`).join('\n')}
  `;

  // Function to handle save
  const saveDocument = async () => {

    console.log('when save click', expenses)

    try {
      let expenseRecent = [];
  
      if (groupId) {
        // Fetch the most recent data if the group already exists
        const response = await axios.get(`/groups/${groupId}`);
        expenseRecent = response.data.expenses || [];
      }
  
      // 1. Handle deletions
      const deletedExpenses = expenseCopy.filter(
        (copyExp) => !expenses.some((exp) => exp._id === copyExp._id)
      );
      const updatedExpenseRecent = expenseRecent.filter(
        (exp) => !deletedExpenses.some((delExp) => delExp._id === exp._id)
      );
  
      // 2. Handle items deleted by others
      const expensesAfterDelete = expenses.filter((exp) => {
        const existsInRecent = updatedExpenseRecent.some((recExp) => recExp._id === exp._id);
        const isNew = exp.status === 'new';
        return isNew || existsInRecent;
      });
  
      // 3. Handle new items added by others
      const finalExpenses = [
        ...expensesAfterDelete,
        ...updatedExpenseRecent.filter(
          (recExp) => !expenses.some((exp) => exp._id === recExp._id)
        ),
      ];
  
      // 4. Resolve conflicts based on status and timestamp
      const mergedExpenses = finalExpenses.map((expense) => {
        const recent = updatedExpenseRecent.find((recExp) => recExp._id === expense._id);
        if (expense.status === 'new') {
          return { ...expense, status: 'db' }; // Prioritize new edits and set status to 'db'
        }
        if (recent && recent.timestamp > expense.timestamp) {
          return recent; // Prioritize the most recent timestamp
        }
        return expense;
      });
  
  
      let newGroupId = groupId; // Initialize with existing groupId
  
      if (groupId) {
        // 5. Update the existing group in the database
        await axios.post(`/groups/${groupId}`, {
          expenses: mergedExpenses.map((exp) => ({
            ...exp,
            status: 'db', // Change status to 'db' after saving
          })),
          participants,
        });
      } else {
        // 6. Create a new group in the database
        const response = await axios.post(`/save-document`, {
          expenses: mergedExpenses.map((exp) => ({
            ...exp,
            status: 'db', // Change status to 'db' after saving
          })),
          participants,
        });
        newGroupId = response.data.groupId;
        setGroupId(newGroupId);  // Update state with the new groupId
  
        // Redirect to the new group URL with the correct groupId
        navigate(`/${newGroupId}`);
        alert(`${t('settlementSummary.saveAlert')} ${t('settlementSummary.URL')}  ${window.location.origin}/${newGroupId}`);
      }

      // Update the local state with the merged expenses
      setExpenseCopy(mergedExpenses);
      setExpenses(mergedExpenses);
      alert(`${t('settlementSummary.saveAlert')}`);
    } catch (error) {
      console.error('Error saving document:', error);
    }
  };

  return (
    <>
    <Card className="mb-3 shadow-sm">
      <Card.Header className='settlement-header'>
         {t('settlementSummary.header')}
      </Card.Header>
      <Card.Body>
        <div className='d-flex justify-content-between'>
          <h6> {t('settlementSummary.totalSpent')}: {formatCurrency(totalExpenses, currency)}</h6>
          <p>{t('settlementSummary.numParticipants')}: {participants.length}</p>
        </div>
        
        {/* Transactions List */}
        <h5 className="mt-4"> {t('settlementSummary.transactions')}</h5>
        {
          transactions.length > 0 ?
          <TransactionsList transactions={transactions.map(tx => ({
            ...tx,
            amount: formatCurrency(tx.amount, currency),  // Format amount here for display
          }))} /> : <p>{t('settlementSummary.noTransactionNeeded')}</p>
        }
        
        {/* Include the Expense List */}
        <h5 className="mt-4"> {t('settlementSummary.detailedExpenses')}</h5>
        <ExpenseList 
          expenses={expenses}
          setExpenses={setExpenses}
          participants={participants}
          removeExpense={removeExpense}
          formatCurrency={formatCurrency} />  

        <Row>
          <Col md={6}>
            {/* Save Button */}
            <Button variant="primary" className="w-100 mt-2" onClick={saveDocument}>
              {t('settlementSummary.save')}
            </Button>
          </Col>
          <Col md={6}>
            {/* KakaoTalk Share Button */}
            <ShareButton groupId={groupId} summaryText={summaryText} saveDocument={saveDocument} />
          </Col>
        </Row>

        
      </Card.Body>
    </Card>
    {
      groupId??(
        <Alert variant='warning'>
          <p><small>{t('groupURLDeleteInfo')}</small></p>
        </Alert>
      )
    }
    
    </>
  );
}

export default SettlementSummary;
