// src/routes/Expenses.js
import React, {useState, useEffect} from 'react';
import { useNavigate } from 'react-router-dom';
import { useParams } from 'react-router-dom';
import { Col, Row, Alert, Button, Form } from 'react-bootstrap';
import { useTranslation } from 'react-i18next';
import axios from './../utils/axios';
import ExpenseForm from '../components/ExpenseForm';  // Adjust the import path if necessary
import SettlementSummary from '../components/SettlementSummary';  // Adjust the import path if necessary

function Expenses({ participants, setParticipants, expenses, setExpenses }) {
    const { t } = useTranslation(); 
    const navigate = useNavigate();

    const { groupId } = useParams(); // Extract groupId from the URL
    const [localGroupId, setLocalGroupId] = useState(groupId || null);
    const [expenseCopy, setExpenseCopy] = useState([]);

    // Redirect to '/' if there are no participants
    useEffect(() => {
        if (groupId) {
            fetchGroupData(groupId);
        } else if (participants.length === 0) {
            navigate('/'); // Redirect to root if no participants and no groupId
        }
    }, [groupId, navigate]);
    
    const fetchGroupData = async (id) => {
        try {
          const response = await axios.get(`/groups/${id}`);
          const groupData = response.data;
          setExpenses(groupData.expenses);
          setExpenseCopy(JSON.parse(JSON.stringify(groupData.expenses))); // Deep copy
          setParticipants(groupData.participants);
        } catch (error) {
          console.error('Error fetching group data:', error);
        }
    };

    const copyToClipboard = () => {
        const url = `${window.location.origin}/expense-settler/${groupId}`;
        navigator.clipboard.writeText(url).then(() => {
        alert('URL copied to clipboard!');
        });
    };

    const removeExpense = (index) => {
        const updatedExpenses = expenses.filter((_, i) => i !== index);
        setExpenses(updatedExpenses);
      };


    return (
    <Row>
       {groupId && (
            <Col md={12} className="mb-2 mt-3">
                <Alert variant="success" >
                    <Form.Label className="mr-auto">
                        {t('groupURLInfo')}:
                    </Form.Label>
                        <div className='d-flex justify-content-center'>
                            <Form.Control value={`${window.location.origin}/expense-settler/${groupId}`} readOnly />
                            <Button className='mx-2' variant="primary" onClick={copyToClipboard}>Copy</Button>
                        </div>                
                </Alert>
            </Col>
      )}
        <Col md={6}>
        <ExpenseForm
            expenses={expenses}
            addExpense={(expense) => setExpenses([...expenses, expense])}
            participants={participants}
            setParticipants={setParticipants}
        />
        </Col>
        <Col md={6}>
        <SettlementSummary 
            groupId={localGroupId}
            setGroupId={setLocalGroupId}
            expenses={expenses}
            setExpenses={setExpenses}
            expenseCopy={expenseCopy}
            setExpenseCopy={setExpenseCopy}
            participants={participants}
            removeExpense={removeExpense}
        />
        </Col>
    </Row>
    );
}

export default Expenses;
